.call-to-action {
    width: 218px;
    position: absolute;
    left: 20px;
    top: 193px;
    display: flex;
    flex-wrap: wrap;

    @include tablet {
        width: 162px;
        left: 34px;
        top: 102px;
    }

    @include desktop {
        width: 278px;
        left: 115px;
        top: 171px;
    }

    &__headline {
        font-family: 'Titan One';
        font-weight: 400;
        font-size: 26px;
        line-height: 1.15;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #FFFFFF;
        padding-bottom: 20px;
        width: fit-content;

        @include tablet {
            font-size: 22px;
            line-height: 1.14;
        }

        @include desktop {
            font-size: 38px;
            padding-bottom: 25px;
        }
    }

    &__headline-accent {
        font-family: 'Titan One';
        font-weight: 400;
        font-size: 48px;
        line-height: 0.96;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: getColor(dark-pink);

        @include tablet {
            font-size: 34px;
            line-height: 0.91;
        }

        @include desktop {
            font-size: 58px;
        }
    }

    &__link {
        display: block;
        width: fit-content;
        height: fit-content;
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 8px;
        line-height: 1.25;
        text-align: center;

        @include desktop {
            font-size: 14px;
            line-height: 1.29;
        }

    }

    &__button1 {
        background: #fff;
        width: fit-content;
        height: 24px;
        border-radius: 12px;
        color: getColor(dark-pink);
        border: 0px;
        padding: 7px 13px;
        transition: background-color $transition-time-timing;


        &:hover {
            cursor: pointer;
            background: #ECEBEB;
        }

        &:visited {
            color: getColor(dark-pink);
        }

        @include desktop {
            height: 40px;
            padding: 11px 13px;
            border-radius: 20px;

        }

        &--accent {
            background: getColor(dark-pink);
            color: #fff;
            margin-right: 7px;

            @include desktop {
                margin-right: 15px;
            }

            &:hover {
                cursor: pointer;
                background-color: getColor(dark-pink);
            }

            &:visited {
                color: #fff;
            }
        }
    }
}